import { ModalHeader, ModalBody, Image, Stack } from "@chakra-ui/react";
import React from "react";
import math1 from "./math1.png";
import math3 from "./math3.png";
import info1 from "./info1.png";
import chem from "./chem.png";
import bio from "./bio.png";
import phy from "./phy.png";
import earth3 from "./earth3.png";
import earth4 from "./earth4.png";

function Content(props) {
  switch (props.area) {
    case "math":
      return (
        <div>
          <ModalHeader>수학 부스</ModalHeader>
          <ModalBody>
            <Stack>
              <Image src={math1} alt="1층 지도" />
              <Image src={math3} alt="3층 지도" />
            </Stack>
          </ModalBody>
        </div>
      );
    case "it":
      return (
        <div>
          <ModalHeader>정보 부스</ModalHeader>
          <ModalBody>
            <Image src={info1} alt="1층 지도 + 메이크스페이스" />
          </ModalBody>
        </div>
      );
    case "phy":
      return (
        <div>
          <ModalHeader>물리 부스</ModalHeader>
          <ModalBody>
            <Image src={phy} alt="실험동 3층 물리 부스" />
          </ModalBody>
        </div>
      );
    case "chem":
      return (
        <div>
          <ModalHeader>화학 부스</ModalHeader>
          <ModalBody>
            <Image src={chem} alt="실험동 2층 화학 부스" />
          </ModalBody>
        </div>
      );
    case "bio":
      return (
        <div>
          <ModalHeader>생명과학 부스</ModalHeader>
          <ModalBody>
            <Image src={bio} alt="실험동 2층 생명과학 부스" />
          </ModalBody>
        </div>
      );
    case "earth":
      return (
        <div>
          <ModalHeader>지구과학 부스</ModalHeader>
          <ModalBody>
            <Stack>
              <Image src={earth3} alt="실험동 3층 생명과학 부스" />
              <Image src={earth4} alt="천문대" />
            </Stack>
          </ModalBody>
        </div>
      );
    default:
      return (
        <div>
          <ModalHeader>뭐야, 내 부스 돌려줘요</ModalHeader>
          <ModalBody>error</ModalBody>
        </div>
      );
  }
}

export default Content;
